@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap);
* {
  margin: 0;
  padding: 0;
  list-style: none;
}
body {
  background: #f6fafb;  
}
.App{
  display: flex;
  align-items: center;
  flex-direction: row;
}
.App .data-content{
  width: 60%;
  height: 100vh;
}
.App .user-escape{
  background-color: #490009;
  width: 40%;
  height: 100vh;
}
.App header{
  padding: 30px;
}
section.header{
    width: 100%;
    display: flex;
    height: 25px;
    align-items: center;
    flex-direction: row;
}
section.header .childFlex {
    flex: auto;
    text-align: center;
    padding: 5px ;
    align-content: space-between;
}
section.header .icon{
    text-align: left;
    color: #490009;
}
section.header .menu{
    text-align: right;
}
section.header .menu a{
    /* margin: 0px 75px; */
    text-decoration: none;
    color: black;
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
}
section.header .menu a:hover{
    color: #bc4558;
    transition: .3s all;
}
section.header .avatar .me{
    border: 2px solid #f6fafb;
    background-color: #d6d6d6;
    border-radius: 50%;
    width: 30px;
    padding: 6px;
    height: 30px;
    float: right;
}
#avatar{
    width: 30px;
}
.content{
    width: 100%;
    padding-left: 60px;
    padding-top: 60px;
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    display: flex;
    flex-direction: column;
    letter-spacing: 1pt;
    margin: 0px auto;
    position: relative;
    z-index: 10;
}
.content .type{
    max-width: 50px;
    padding-left: 3px;
    padding-bottom: 30px;
}
.content .name,
.content .description{
    max-width: 60%;
    padding-bottom: 30px;
}
.content h2{
    font-size: 11pt;
    text-transform: uppercase;
}
.content label{
    font-size: 40pt;
    text-transform: capitalize;
}
.content .description{
    color: #490009;
}
.content .btn-randomize{
    align-self: center;
}
.content .btn-randomize button{
    padding: 15px;
    border: 0px;
    background-color: #4900095e;
    color: white;
    cursor: pointer;
    border-radius: 3px;
    font-family: 'Roboto', sans-serif;
}
.content .btn-randomize button:hover{
    background-color: #490009;
    transition: .3s all;
}
.padding{
    padding-top: 120px;
}
.widget {
    display: flex;
    justify-content: center; /* center items horizontally, in this case */
    align-items: center;     /* center items vertically, in this case */
    width: 450px;
    flex-flow: column;
    background-color: #9b7478;
    margin: 0px auto;
    position: relative;
    left: -40%;
    border-radius: 3px;
    /* box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.397); */
}
.widget img {
    width: 250px;
    align-self: flex-start;
    transform: scaleX(-1);
}
.widget .location{
    position: absolute;
    right: -30px;;
    color: #490009;
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    text-transform: uppercase;
    font-size: 11pt;
    transform: rotate(90deg);
}
